@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  font-family: 'Quicksand';
}
:root{
  --gunmetal: #022b3a;
  --gunmetaltrans: #022b3a63;
  --teal : #1F7A8C;
  --columbiablue : #BFDBF7;
  --p1 : #344E41;
  --p2 : #3A5A40;
  --p3 : #588157;
  --p3_1:#3c573b57;
  --p4 : #A3B18A;
  --p5 : #DAD7CD;
  --lavender: #E1E5F2;
}
/* BackGround */
.App::before{
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.7;
  background: url(./bg.jpeg) no-repeat center center/cover ;
}
.App{
  display: flex;
  justify-content: left;
  margin: auto;
  padding: auto;
  
}
.home{
  padding: 0 20px ;
}
/* navigation */
.navigation {
  position: absolute;
  left: -80px;
  width: 100px;
  height: 100vh;
  display: flex;
  align-items: center;
  transition: all .5s;
  
}

.navigation ul{
  height: 80vh;
  width: 100px;
  display: flex;
  flex-flow: column;
  box-shadow: 2px 2px 5px rgb(106, 102, 102);
  background-color: var(--p5);
  justify-content: space-evenly;
  align-items: end;
  border-radius:25px;
  border: 2px solid var(--p1);
  
}
.navigation ul a{
  padding: 2px;
  margin: 3px;
  text-decoration: none;
  color:var(--p1);
  display: block;
  width: 70px;
  text-align: left;
  font-size: 16px;
  font-weight: 600;
  border-radius: 7px;
  
}
.navigation:hover{
  left: -20px;
}
.navigation ul a:hover{
  background-color: var(--gunmetal);
  color: var(--columbiablue);
}
/* infobar */
.infobar{display: flex;
  width: 100%;
  height: 50px;
  display: flex;
  padding: 0 2px;
  align-items: center;
}
.infobar img{
  width: 39px;
  height: 39px;
  margin: 2px 10px;
  border-radius: 19px;
  border: 2px solid var(--p1);
}
.infobar h1{
  margin: 0 10px;
}

/* Calendar css  */
.calendarDiv{
  margin:  20px 20px;
  background-color: var(--p3_1);
  display: inline-block;
  align-items: center;
  justify-content: center;
}
.calendarDiv iframe{
  margin: 10px;
  border:solid 1px #777;
  width:400px;
  height:300px;
  border: 0;             
}

/* Tools Links css */
.linkBars {
  display: flex;
  width: 740px;
  justify-content: center;
  align-items: center;
  height:max-content;
  flex-wrap: wrap;
}
.toolsLink{
  background-color: var(--p3_1);
  width: 200px;
  height: 40vh;
  
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.toolsLink ol{
 
  width: 200px;
  
  height: 450px;
  display: flex;
  justify-content: top;
  flex-direction: column;
  
  overflow:scroll;
  
  /* background-color: var(--gunmetaltrans); */
}
.toolsLink ol li{
  margin-top: 0;
  
  padding: 8px;
  
  margin: 2px;
  font-size: 17px;
  color: var(--gunmetal);
  
}

.toolsLink ol li a{
  text-decoration: none;
  padding: 8px;
  border-radius: 13px;
  color: var(--gunmetal);
  font-weight: 600;
}
.toolsLink ol li a:hover{
  color: var(--lavender);
  background-color: #022b3a;

}

.linkBars{
  display: flex;

}
.toolsLink h2{
  color: var(--gunmetal);
  padding: 3px;
  margin: 3px;
  font-weight: 1000;
}

/* CalenderDivDetails */
.calendarDivD{
  width: 80vw;
  height: 100vh;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  margin: 0 10vw;
  font-weight: 800;
  font-size: 30px;
}
.calendarDivD iframe{
  width: 80vw;
  height: 45vw;
}